
import axios from "axios"
const initialHttpReducerState={
    fetching:false,
    fetched:false,
    ConversionTableFields:[],
    data4:[],
    object_fields:[],
    tamplate:'',
    error:null,
    tampletes_name:[]
  };

export default function HttpReducer(state=initialHttpReducerState,action){
    switch(action.type){
      case "FETCH_ERROR":{
        return{...state,fetching:false,error:action.payload}
      }
  
      case "GET_CONVERSION_TABLE":{
          console.log(action.payload.data);
        return{...state,fetching:false,fetched:true,ConversionTableFields:action.payload.data}
      }
      case "GET_TAMPLETS_NAMES":{
        console.log(action.payload.data);
      return{...state,fetching:false,fetched:true,tampletes_name:action.payload.data}
    }
      case "GET_TAMPLETE":{
      return{...state,fetching:false,fetched:true,tamplate:action.payload.data}
    }
  
    case "RECIVE_USERS4":{
      return{...state,fetching:false,fetched:true,data4:action.payload.data}
    }
    

      default:{
        break;
      }
    }
  return state;
  }