  makeTableCell(value, color = 'white') {
    return `<td style="background:${color}"> ${value} </td>`
  }
  split($table, chunkSize) {
    var cols = $("th", $table).length - 1;
    var n = cols / chunkSize;

    for (var i = 1; i <= n; i++) {
      $("<br/>").appendTo("body");
      var $newTable = $table.clone().appendTo("body");
      for (var j = cols + 1; j > 1; j--) {
        if (j + chunkSize - 1 <= chunkSize * i || j > chunkSize * i + 1) {
          $('td:nth-child(' + j + '),th:nth-child(' + j + ')', $newTable).remove();
        }
      }
    }
  }
  makeTableRow(valueArr, numColumns) {
    console.log(valueArr)
    let res = "<tr >"

    for (let i = 0; i < numColumns; i++) {
      if (valueArr[i] !== undefined)
        res += this.makeTableCell(valueArr[i].value, valueArr[i].color)
    }
    res += "</tr>"
    console.log(valueArr)
    return res
  }
  makeTableRows(valueArr, numColumns) {
    console.log(valueArr)
    let res = ""
    for (let i = 0; i < valueArr.length; i++) {
      res += this.makeTableRow(valueArr[i], numColumns)
    }
    return res
  }



