  changeLayout(layout_num) {
    switch (layout_num) {
      case '1': {
        window.CKEDITOR.config.height = '35cm'
        this.setState({ layout_number: '1' })
        this.remove_4_layout_classes()
        this.add_1_layout_classes()
        this.fillLayoutDisplayArr([1, 5, 5, 5])
        break;
      }
      case '2': {
        window.CKEDITOR.config.height = '35cm'
        this.setState({ layout_number: '2' })
        this.remove_4_layout_classes()
        //this.remove_2_layout_classes()

        this.fillLayoutDisplayArr([2, 2, 5, 5])
        this.add_2_layout_classes()
        break;
      }
      case '4': {
        window.CKEDITOR.config.height = '16cm'
        this.setState({ layout_number: '4' })
        this.remove_2_layout_classes()
        this.add_4_layout_classes()
        this.fillLayoutDisplayArr([4, 4, 4, 4])
        break;
      }
      default: break;
    }
  }
  convert_from_2_layout_to_4_layout() {
    let one_from_layout4 = document.querySelectorAll(".one_from_layout4");

    for (let i = 0; i < 2; i++) {
      one_from_layout4[i].classList.remove("one_from_layout4")
      one_from_layout4[i].classList.add("one_from_layout2")
    }
    for (let i = 2; i < 4; i++) {
      one_from_layout4[i].classList.remove("one_from_layout4")
      one_from_layout4[i].classList.add("display_none")
    }
  }
