
import axios from "axios"

export const url_private_server='http://localhost:8000/App/api/reportApp/';
export const url_test_server='http://localhost:7000/rest/ReportApp/';
//export const url_private_server=url_test_server


//get Part Tamplete
export  const GetPartTampleteHttp =  (url) => {
  console.log(url)
  return async function (dispatch) {
    axios.get('http://localhost:7000/rest/ReportApp/GetPartTemplate')
      .then((response) => {
        console.log(response)
          dispatch({ type: "GET_PART_TAMPLETE", payload: response })
      })
      .catch((err) => {
        dispatch({ type: "FETCH_ERROR", payload: err })
      })
  }
}








