
import axios from "axios"

export const url_private_server='http://reportjavaapp-env.eba-iuq3zppe.us-east-2.elasticbeanstalk.com/api/reportApp/';
//export const url_test_server='http://localhost:7000/rest/ReportApp/';
//export const url_private_server=url_test_server
export function GetAllTemplatesNames() {
  return function (dispatch) {
    axios.get( url_private_server + "GetAllTemplatesNames")
      .then((response) => {
        dispatch({ type: "GET_TAMPLETS_NAMES", payload: response })
      })
      .catch((err) => {
        dispatch({ type: "FETCH_ERROR", payload: err })
      })
  }
}

