
import axios from "axios"
const initialState={
    fetching:false,
    fetched:false,

    part_tamplate:'',
    error:null,

  };

export default function PartTemplateHttpReducer(state=initialState,action){
    switch(action.type){
      case "FETCH_ERROR":{
        return{...state,fetching:false,error:action.payload}
      }
      case "GET_PART_TAMPLETE":{
      console.log(action.payload.data);
      return{...state,fetching:false,fetched:true,part_tamplate:action.payload.data}
    }
    
      default:{
        break;
      }
    }
  return state;
  }