import React from 'react';
import Page1Layout from './Page1Layout';
import   './Page1.css';
export default class Page1 extends React.Component {

  constructor(props) {
    super(props);
    this.state = {open: false,tamplateName:'',Query:''};
  }
  getTemplateName(){
    

    let startQuery = this.props.location.search.search("Query")
    let startTemplateName = this.props.location.search.search("FileName")
    let TemplateName =this.props.location.search.substr(startTemplateName,startQuery-2).replace(/%20/g, " ")
    TemplateName = TemplateName.replace('FileName=','');
    console.log('77777777777',TemplateName)
    console.log(decodeURIComponent(TemplateName.replace(/\+/g,  " ")))
    TemplateName = decodeURIComponent(TemplateName.replace(/\+/g,  " "))
    return TemplateName
  }
  getQueryValue(){
    let startQuery = this.props.location.search.search("Query")
    let Query =this.props.location.search.substr(startQuery,this.props.location.search.length).replace(/%20/g, " ")
    Query = Query.replace('Query=','');
    console.log('77777777777',Query)
    Query = decodeURIComponent(Query.replace(/\+/g,  " "))
    return Query
  }
  componentDidMount() {
  }

  render() {
    return (      

      <div>
        {/*<div className="wysiwyg-page"  > Bootstrap A4 WYSIWY</div>*/}
        <Page1Layout name="momo"  tamplate="<p>Hello from CKEditor 5</p>"
        data=""
        tamplateName={this.getTemplateName()}
        Query={this.getQueryValue()}
        />
      </div>
    );
  }
}