import React from 'react';
import { connect } from 'react-redux';
import {SubmitQuery} from '../actions/QueryActions';
import store from '../store';
class Query extends React.Component {

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(event) {
    let nam = event.target.name;
    let val = event.target.value;
    this.setState({[nam]: val});
    this.setState(prevState => ({
      DBname: prevState.DBname1+'.'+prevState.DBname2+'.'+prevState.DBname3
    }));
    console.log(this.state)
  }

  handleSubmit(event) {
      if (this.state.DBname1===undefined || this.state.DBname2===undefined||this.state.DBname3===undefined) {
        alert("Your missed one of the values");
      }
      console.log(this.state)
  this.props.SubmitQuery(this.state)
  }

  render() {
    return (

      <div>
     
        <input placeholder="הכנס קטגוריה בעברית" onChange={this.handleChange}  name='HebrewCategory'/>
          <input placeholder="הכנס שם עברי" onChange={this.handleChange}  name='Hebrewname'/>
          <input placeholder="הכנס שם טבלה (אנגלית)"  onChange={this.handleChange}  name='DBname1'/>
          <input placeholder="הכנס שם עמודה (אנגלית)"  onChange={this.handleChange}  name='DBname2'/>
          <input placeholder="הכנס תנאי(אנגלית)"  onChange={this.handleChange}  name='DBname3'/>
          <button className="grayBtn" onClick={()=>this.handleSubmit()}>הכנס ערך לטבלת המרה</button>
      
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch, state) => {
  return {
    SubmitQuery(querystate) {

      dispatch(SubmitQuery(querystate))

    }

  }
}
const mapStateToProps = (state, ownProps) => {
  console.log(state)
  return {
    ConversionTableFields: state.HttpReducer.ConversionTableFields,
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Query)